.profile__subtitle {
  margin: 0;
  padding: 0;

  font-size: 18px;
  line-height: 1.22;

  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media all and (max-width: 1023px) {
  .profile__subtitle {
    text-align: center;
    font-size: 14px;
    line-height: 1.21;
  }
}